<template>
    <section class="newTable" v-if="options">
      <table class="table datatable table-striped" :id="prefix">
        <thead>
          <tr>
            <th v-if="options.checkAll" :width="options.checkBoxHasUser ? 110 : 60">
              <div class="checkbox checkbox-inline mr-0" v-if="multiple">
                <input type="checkbox" name="check-all" id="check-all" value="1" v-model="checkAll">
                <label for="check-all"></label>
              </div>
              <span v-if="options.checkBoxHasUser">#</span>
            </th>
            <th
            v-for="(item, index) in options.columns"
            :key="`${prefix}-table-tr-${index}`"
            :width="(index+1===options.columns.length && options.scrollHeight ? iPadWidth : item.width) || '*'"
            :class="item.sorting ? 'sorting ' + (order.index==index + 1 ? (order.asc ? 'sorting_asc' : 'sorting_desc') : '') : ''"
            @click="columnSorting(index + 1)"
            >
                <span v-html="item.label"></span>
            </th>
          </tr>
        </thead>
        <tbody @scroll="scrollmethod" v-if="tableData.length" :class="options.scrollHeight ? 'block-height' : ''" :style="{height: options.scrollHeight + 'px'}">
          <tr v-for="(d, did) in tableData" :key="`${prefix}-table-td-${did}`">
            <td v-if="options.checkAll" :width="options.checkBoxHasUser ? 110 : 60">
              <div v-if="multiple" class="mr-0 checkbox checkbox-inline">
                <input
                type="checkbox"
                :name="`td-item${multiple? '[]' : ''}`"
                :id="`${prefix}-check-${did}`"
                :value="d.id"
                v-model="selection"
                @change="$emit('update', selection)">
                <label :for="`${prefix}-check-${did}`"></label>
              </div>
              <div v-else class="mr-0 radio radio-inline">
                <input
                type="radio"
                :name="`td-item${multiple? '[]' : ''}`"
                :id="`${prefix}-check-${did}`"
                :value="d.id"
                v-model="selection"
                @change="$emit('update', selection)">
                <label :for="`${prefix}-check-${did}`"></label>
              </div>
              
              <span v-if="options.checkBoxHasUser">
                <span class="no">{{d.index}}</span>
                <span class="info text-center">
                    <level :lv="Number(d.vip_level)" />
                    <avatar :gender="Number(d.sex)" :size="20" />
                </span>
              </span>
            </td>
            <td
            v-for="(col, cid) in options.columns"
            :width="col.width || '*'"
            :key="`table-td-col-${cid}`">
                <span v-if="col.template" v-html="col.template(d[col.name])"></span>
                <span v-else-if="col.name==='birthday'">
                  <div class="date">{{d[col.name] | dateFormat}}</div>
                  <div class="age">
                    <!-- <b>{{ d[col.name] | ageCounter }}</b>歲 -->
                    <b>{{ d.age? d.age : '— —' }}</b>歲
                  </div>
                </span>
                <span v-else-if="col.name==='idmark'">
                  <div class="list inline">
                    <span class="tag-box" :class="d[col.name].indexOf('1') > -1 ? 'active': ''">
                      要
                    </span>
                    <span class="tag-box" :class="d[col.name].indexOf('2') > -1 ? 'active': ''">
                      被
                    </span>
                    <span class="tag-box" :class="d[col.name].indexOf('3') > -1 ? 'active': ''">
                      意
                    </span>
                    <span class="tag-box" :class="d[col.name].indexOf('4') > -1 ? 'active': ''">
                      產
                    </span>
                    <span class="tag-box" :class="d[col.name].indexOf('5') > -1 ? 'active': ''">
                      團
                    </span>
                    <span class="tag-box" :class="d[col.name].indexOf('6') > -1 ? 'active': ''">
                      借
                    </span>
                  </div>
                </span>
                <a v-else-if="col.click" @click="col.click(d)" class="link text-underline">{{d[col.name]}}</a>
                <span v-else>{{d[col.name]}}</span>
            </td>
          </tr>
          <tr v-if="scrollMessageLoading">
            <td colspan="8" class="text-center pt-3 pb-5">
              <h3>
                資料載入中
                <span>. . .</span>
              </h3>
            </td>
            <td style="display: none;"></td>
            <td style="display: none;"></td>
            <td style="display: none;"></td>
            <td style="display: none;"></td>
            <td style="display: none;"></td>
            <td style="display: none;"></td>
            <td style="display: none;"></td>
          </tr>
          
        </tbody>
        <tbody v-else>
          <tr>
            <td colspan="8" class="empty-block">
              <h3>{{remoteDataMsg}}</h3>
            </td>
            <td style="display: none;"></td>
            <td style="display: none;"></td>
            <td style="display: none;"></td>
            <td style="display: none;"></td>
            <td style="display: none;"></td>
            <td style="display: none;"></td>
            <td style="display: none;"></td>
          </tr>
        </tbody>
      </table>
    </section>
</template>

<script>
import Level from '../Level'
import Avatar from '../Avatar'
import { commonMixin } from '../../mixins/common'
import { setTimeout } from 'timers';

const is_iPad = navigator.userAgent.match(/iPad/i) != null // 確認 裝置是否為 Ipad
let idmarkWidth = 117
if(is_iPad){
  idmarkWidth = 100
}


export default {
    name: "DataTable",
    mixins: [commonMixin],
    components: {Level, Avatar},
    props: {
        multiple: {
            type: Boolean,
            default: true
        },
        prefix:{
            type: String,
            default: ''
        },
        remoteDataMsg:{
            type: String,
            default: ''
        },
        currentPage:{
            type: Number,
            default: 0
        },
        totalpages:{
            type: Number,
            default: 0
        },
        scrollDataLoading:{
            type: Boolean,
            default: false
        },
        scrollMessageLoading:{
            type: Boolean,
            default: false
        },
        tableData: {
            type: Array,
            default: () => {
                return []
            }
        },
        options: {
            type: Object,
            default: () => {
                return null
            }
        }
    },
    data() {
        return {
            tableList: this.tableData,
            order: {
                index: 0,
                asc: true
            },
            checkAll: 0,
            selection: [],
            currentWidth: 0,
            nowTime:0,
            lastTime:0,
            iPadWidth: idmarkWidth,
        }
    },
    watch: {
      "checkAll": "doCheckAll"
    },
    methods: {
      scrollmethod(e){
        this.nowTime = +new Date()
        if(this.nowTime > this.lastTime + 150){
          this.lastTime = this.nowTime
          if (!this.scrollDataLoading && e.target.scrollTop > (e.target.scrollHeight - 700)) {
            this.options.scrollLoadingAPI()
          }
        }
      },
      doCheckAll(val) {
        if(val) {
          this.tableData.forEach(data => {
            if(data.id !== '') 
              this.selection.push(data.id)
          });
        } else {
          this.selection = []
        }

        this.$emit('update', this.selection)
      },
      columnSorting (i) {
            if(this.order.index===i) {
                this.order.asc = !this.order.asc
            }
            else {
                this.order.index = i
                this.order.asc = true
            }
            this.options.sorting(i + 1)
        }
    },
    mounted () {
        this.currentWidth = this.$el.clientWidth * (this.$el.clientWidth < 1250 ? 0.95 : 1)
    }
}
</script>

<style lang="scss" scoped>
.newTable thead tr,
.newTable tbody tr {
  display: table;
  width: 100%;
  table-layout: fixed;
  border-left: 0;
  border-right: 0;
  border-bottom: 0;

  td {
    word-break: break-all;
  }
  
  &.sorting,
  &.sorting_desc {
    position: relative;
    padding-right: 25px !important;
    
    &:before,
    &:after {
      content: '';
      position: absolute;
      width: 0px;
      height: 0px;
      display: block;
      top: 0;
      bottom: 0;
      right: 6px;
      margin: auto;
    }

    &:after {
      top: 18px;
      border-top: 6px solid transparentize(#027A39, .5);
      border-left: 5px solid transparent;
      border-right: 5px solid transparent;
      border-bottom: 6px solid transparent;
    }

    &:before {
      top: -13px;
      border-bottom: 6px solid transparentize(#027A39, .5);
      border-left: 5px solid transparent;
      border-right: 5px solid transparent;
      border-top: 6px solid transparent;
    }
  }

  &.sorting_asc:before {
    border-bottom: 6px solid transparentize(white, .5);
  }

  &.sorting_desc:after {
    border-top: 6px solid transparentize(white, .5);
  }

  &.no-sort:before,
  &.no-sort:after {
    display: none;
  }
}


.newTable tbody.block-height {
  display: block;
  height: 350px;
  overflow-y: scroll;
  border: 0 !important;
  -webkit-overflow-scrolling: touch;

  &::-webkit-scrollbar {
    width: 17px; 
    border: 0;
    box-shadow: none;
  }
  
  &::-webkit-scrollbar-track
  {
    box-shadow: none;
    border-radius: 0px;
    border: 0;
    background-color: #F5F5F5;
  }

  &::-webkit-scrollbar-thumb
  {
    width: 10px;
    border-radius: 20px;
    background-color: #555;
  }
}
</style>
