        :options="tableOption" 
        :table-data="tableData"
        :currentPage="currentPage"
        :totalpages="totalpages"
        :scroll-data-loading="scrollDataLoading"
        :scroll-message-loading="scrollMessageLoading"
        :remote-data-msg="remoteDataMsg"/>
      </div>
    </div>

    <section class="container-fluid">
      <div class="row">
        <div class="col-12">
        </div>
        <div class="col-9 col-sm-8 col-md-8">
          <div class="list inline">
            <div class="item">
              <span class="tag-box">要</span>
              <small class="text">壽險要保人</small>
            </div>
            <div class="item">
