    </section>

    <print-modal
    :open="printModalOpen"
    :custs="selection"
    @close="panelClose('printModalOpen')"/>

    <group-modal
    :open="groupModalOpen"
    :custs="selection"
    @submit="submitGroup"
    @showError="errorGroup"
    @close="panelClose('groupModalOpen')"/>
