<script>
import { commonMixin } from '../../mixins/common'
import DataTable from '../../components/DataTable/DataTableScroll'
import { mapActions, mapGetters } from 'vuex'
import { getCustomers } from '../../service'

import Level from '../components/Level'
import Avatar from '../../components/Avatar'
import PrintModal from '../components/PrintModal'
import GroupModal from '../components/GroupModal'
import { setTimeout } from 'timers';
import { SearchResultMock } from '../../mock'



export default {
  name: 'SearchResult',
  mixins: [commonMixin],
  watch: {
    checkAll: 'toggleCheckAll',
    customerType : function(newAry, oldAry) {
      // let oldSet = new Set(oldAry)
      // let addTag = newAry.filter(tag => !oldSet.has(tag))
      // if(addTag.length) {
      //   if(addTag[0] == '6') {
      //     this.customerType = ['6']
      //   } else {
      //     this.customerType = newAry.filter(tag => tag != '6')
      //   }
      // }
      this.doSearch()
    }
  },
  components: {
    PrintModal, GroupModal, Level, Avatar, DataTable
  },
  computed: {
    ...mapGetters(['BaseUrl', 'QueryKeyword', 'SliceMode'])
  },
  data () {
    return {
      remoteDataMsg: "資料讀取中，請稍候...",
      currentWidth: 0,
      currentScoll: 0,
      groupModalOpen: false,
      printModalOpen: false,
      checkAll: false,
      selection: [],
      customerType: ['1','2','3','4','5','6'],
      order:{
        order_by:'2',
        order_asc: true,
      },
      page:'1',
      currentPage: 0,
      scrollDataLoading: false,
      scrollMessageLoading: false,
      totalpages: 0,
      keyowrd: '',
      resultTable: null,
      tableOption: {
        checkAll: true,
        scrollHeight: 350,
        isScrollLoading: true,
        checkBoxHasUser: true,
        columns: [
          {
            width: 100,
            name: "name",
            label: "姓名",
            sorting: true,
            click: (data) => {
              this.routeToCustDetail(data.index, {custId: data.id, serNo:data.serNo})
            }
          },
          {
            name: "id",
            label: "身分證",
            sorting: true
          },
          {
            width: 120,
            name: "mobile",
            label: "手機",
            sorting: true
          },
          {
            width: 120,
            name: "phone",
            label: "電話",
            sorting: true
          },
          {
            name: "birthday",
            label: "生日",
            sorting: true
          },
          {
            name: "address",
            sorting: true,
            label: "住家地址"
          },
          {
            width: 100,
            name: "idmark",
            label: "公司往來"
          }
        ],
        sorting: (order_by) => {
          this.tableData = []
          if(this.order.order_by == order_by){
            this.order.order_asc = !this.order.order_asc
          }else{
            this.order.order_asc = true
          }
          this.order.order_by = order_by
          this.getCustomersAndSetup(this.$store.state.userInfo.id, this.keyowrd)
        },
        scrollLoadingAPI: () => {
          this.scrollDataLoading = true
          this.scrollMessageLoading = true
          this.currentPage = this.currentPage + 1
          console.log("load more", 'page:'+　this.currentPage)
          this.getCustomersPage(this.$store.state.userInfo.id, this.keyowrd)
        }
      },
      tableData: []
    }
  },
  methods: {
    ...mapActions([
      'setPageLock' , 'updateCustID' , 'updateQueryKeyword' , 'updateSerNo'
    ]),
    updateSelection (val) {
      this.selection = val
    },
    openGroupModal () { // 判斷無ID客戶才能開啟 modal
      // let checkID = /^\d{8}/g
      let aryHasSerNo = false
      this.selection.forEach(item => {
        if(item === ''){
          aryHasSerNo = true
        }
      })
      if(aryHasSerNo){
        this.$toast.info({title: '選擇項目不得有 (無身分證) 用戶'})
      }else{
        this.panelOpen('groupModalOpen')
      }
    },
    openPrintModal () { // 判斷無ID客戶才能開啟 modal
      // let checkID = /^\d{8}/g
      let aryHasSerNo = false
      this.selection.forEach(item => {
        if(item === ''){
          aryHasSerNo = true
        }
      })
      if(aryHasSerNo){
        this.$toast.info({title: '選擇項目不得有 (無身分證) 用戶'})
      }else{
        this.panelOpen('printModalOpen')
      }
    },
    submitGroup (group) {
      // add group methods

      this.panelClose('groupModalOpen')
    },
    errorGroup (errList) {
      let errMsg = ''
      let copmreMap = {}
      
      this.tableData.forEach(item => {
        console.log(item)
        let cust = item.id
        copmreMap[item.id] = item.name
      })
      console.log(this.compreMap)

      errList.forEach(ele => {
            
              errMsg = errMsg + '客戶: ' + copmreMap[ele] + ' 已達群組上限，\n'
            });

            errMsg = errMsg + '如需調整請至群組管理!!'

      this.$toast.info({title: errMsg})
      this.panelClose('groupModalOpen')
    },
    toggleCheckAll (val) {

      if (val) {
        this.selection = this.tableData.map(item => {return item.id})
        
      } else {
        this.selection = []
      }
    },
    routeToCustDetail(id, {custId = '',serNo = ''}) {
      if(custId !== ''){
        this.updateCustID(custId)
        this.updateSerNo('')
        localStorage.CustID = custId
        localStorage.SerNo = ''
        this.$router.push({path: `/detail/CustID/tab/1`})
      }else{
        this.updateSerNo(serNo)
        this.updateCustID('')
        localStorage.CustID = ''
        localStorage.SerNo = serNo
        this.$router.push({path: `/detail/SerNo/tab/1`})
      }
    },
    async getCustomersAndSetup(agntId, keyword) {
      let s1 = this.customerType.includes('1') ? '1' : 'N'
      let s2 = this.customerType.includes('2') ? '1' : 'N'
      let s3 = this.customerType.includes('3') ? '1' : 'N'
      let s4 = this.customerType.includes('4') ? '1' : 'N'
      let s5 = this.customerType.includes('5') ? '1' : 'N'
      let s6 = this.customerType.includes('6') ? '1' : 'N'
      let order_by = this.order.order_by
      let order_asc = this.order.order_asc === true ? '1' : '2'
      let page = this.page
      this.remoteDataMsg="資料讀取中，請稍候..."
      this.updateQueryKeyword(this.keyowrd)
      this.selection = []
      this.checkAll = false
      this.scrollDataLoading = false

      getCustomers(agntId, keyword, s1, s2, s3, s4, s5, s6, page, order_by, order_asc,'200').then(result => {
        if(result.body.customer_list) {
          this.totalpages = Number(result.body.totalpages)
          this.tableData = result.body.customer_list.map(eachCust => {
            let idmarkSource1 = eachCust.idmark.source1 && eachCust.idmark.source1 == '1' ? '1': 'N'
            let idmarkSource2 = eachCust.idmark.source2 && eachCust.idmark.source2 == '1' ? '2': 'N'
            let idmarkSource3 = eachCust.idmark.source3 && eachCust.idmark.source3 == '1' ? '3': 'N'
            let idmarkSource4 = eachCust.idmark.source4 && eachCust.idmark.source4 == '1' ? '4': 'N'
            let idmarkSource5 = eachCust.idmark.source5 && eachCust.idmark.source5 == '1' ? '5': 'N'
            let idmarkSource6 = eachCust.idmark.source6 && eachCust.idmark.source6 == '1' ? '6': 'N'
            return {
              index: eachCust.index || '',
              vip_level : Number(eachCust.vip_level) || 0,
              sex : Number(eachCust.sex) || 0,
              name : eachCust.name || '',
              age: eachCust.age || '',
              id : eachCust.id || '',
              serNo : eachCust.ser_no || '',
              mobile : eachCust.mobile || '',
              phone : eachCust.phone || '',
              birthday : eachCust.birthday || '',
              address : eachCust.address || '',
              index : eachCust.index || '',
              idmark : [idmarkSource1, idmarkSource2, idmarkSource3, idmarkSource4, idmarkSource5, idmarkSource6],
            }
          })
        } else {
          this.tableData = []
        }
        this.currentPage = 1
        if(this.currentPage == this.totalpages){
          this.scrollDataLoading = true
          this.scrollMessageLoading = false
        }else{
          this.scrollMessageLoading = false
        }
        
        this.remoteDataMsg="查無資料，請確認您輸入之資料是否正確"
      })
    },
    async getCustomersPage(agntId, keyword) {
      let s1 = this.customerType.includes('1') ? '1' : 'N'
      let s2 = this.customerType.includes('2') ? '1' : 'N'
      let s3 = this.customerType.includes('3') ? '1' : 'N'
      let s4 = this.customerType.includes('4') ? '1' : 'N'
      let s5 = this.customerType.includes('5') ? '1' : 'N'
      let s6 = this.customerType.includes('6') ? '1' : 'N'
      let order_by = this.order.order_by
      let order_asc = this.order.order_asc === true ? '1' : '2'
      let currentPage = Number(this.currentPage)
      getCustomers(agntId, keyword, s1, s2, s3, s4, s5, s6, currentPage, order_by, order_asc,'200').then(result => {
        let newTableData = result.body.customer_list.forEach(eachCust => {
          let idmarkSource1 = eachCust.idmark.source1 && eachCust.idmark.source1 == '1' ? '1': 'N'
          let idmarkSource2 = eachCust.idmark.source2 && eachCust.idmark.source2 == '1' ? '2': 'N'
          let idmarkSource3 = eachCust.idmark.source3 && eachCust.idmark.source3 == '1' ? '3': 'N'
          let idmarkSource4 = eachCust.idmark.source4 && eachCust.idmark.source4 == '1' ? '4': 'N'
          let idmarkSource5 = eachCust.idmark.source5 && eachCust.idmark.source5 == '1' ? '5': 'N'
          let idmarkSource6 = eachCust.idmark.source6 && eachCust.idmark.source6 == '1' ? '6': 'N'
          this.tableData.push({
            index: eachCust.index || '',
            age: eachCust.age || '',
            vip_level : Number(eachCust.vip_level) || 0,
            sex : Number(eachCust.sex) || 0,
            name : eachCust.name || '',
            id : eachCust.id || '',
            serNo : eachCust.ser_no || '',
            mobile : eachCust.mobile || '',
            phone : eachCust.phone || '',
            birthday : eachCust.birthday || '',
            address : eachCust.address || '',
            index : eachCust.index || '',
            idmark : [idmarkSource1, idmarkSource2, idmarkSource3, idmarkSource4, idmarkSource5, idmarkSource6],
          })
        })
        if(this.currentPage !== this.totalpages){
          this.scrollMessageLoading = false
          this.scrollDataLoading = false
        }else{
          this.scrollMessageLoading = false
        }
        this.remoteDataMsg="查無資料，請確認您輸入之資料是否正確"
      })
    },
    doSearch () {
      this.tableData = []
      this.getCustomersAndSetup(this.$store.state.userInfo.id, this.keyowrd)
    }
  },
  mounted () {
    this.keyowrd = this.QueryKeyword

    // F5 方式

    // if(this.$store.state.userInfo.id === ''){
    //   setTimeout(() => {
    //     this.getCustomersAndSetup(this.$store.state.userInfo.id, this.QueryKeyword)
    //   }, this.$store.state.timeoutVal);
    // }else{
    //   this.getCustomersAndSetup(this.$store.state.userInfo.id, this.QueryKeyword)
    // }

    this.getCustomersAndSetup(this.$store.state.userInfo.id, this.QueryKeyword)
  }
}
</script>

